*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body{
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}
.App {
  text-align: center;
  display: flex;
  max-width: 70%;
  margin: 20px auto;
  text-align: center;
  flex-direction: column;
}
.form{
  flex: 1;
  padding: 40px;
  background-color: #1234;
  height: auto;


}
.form h1{
  color: rgb(19, 18, 18);
}
.form label{
  text-align: left;
  font-size: 18px;
  font-weight: 500;
  color: rgb(39, 38, 38);
  width: 50%;
  display: block;
  margin: 0 auto;
}
.form input{
  display: block;
  height: 24px;
  width: 50%;
  padding: 8px 5px;
  font-size: 18px;
  outline: none;
  border-radius: 3px;
  border: none;
  color: rgba(32, 34, 36, 0.467);
  margin: 0px auto 10px;
}
.form button{
  display: block;
  width: 50%;
  text-align: center;
  margin: 0 auto;
  padding: 5px;
  font-size: 18px;
  background-color: rgb(19, 18, 18);
  color: #ffff;
  border: none;
  outline: none;
  border-radius: 3px;
}
.content{
  flex: 1;
  text-align: left;
  background-color: #ffff;
  padding: 25px;
}
.content ul li{
  font-size: 16px;
  font-weight: 500;
  list-style: upper-latin;
  margin: .2em;
  padding:5px;

}
.content ul li:hover{
  background-color: #1234;
  color: #ffff;
  
}
.todolist{
  width: 100%;
  display: flex;
  
}
.todolist p, span{
  flex: 1;
  background-color: #f4f4f4;
  color: rgb(19, 18, 18);
  margin: 5px 0;
  padding: 10px;
  font-size: 18px;
  font-weight: 500;
}
.todolist span{
  text-align: right;
  
}
.todolist p:hover, span:hover{
  color: rgb(131, 125, 122);
  cursor: pointer;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}



